body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bar-container {
  display: flex;
  float: right;
  margin-left: 10px;
  justify-content: space-between;
  width: 30px;
  height: 80px;
}

.bar {
  width: 20%;
  height: 0;
  background-color: #759dec;
  border-radius: 5px;
}

.middle {
  height: 0;
}

.hidden {
  visibility: hidden;
}

.shadow-box {
  box-shadow: 1px 1px 5px rgba(10, 9, 9, 0.7), 2px 2px 4px rgba(0, 0, 0, 0.15);
  border-radius: 4vh;
}

.shadow-back-button {
  box-shadow: inset 4px 4px 8px 1px rgba(0, 0, 0, 0.2),
    inset -4px -4px 8px 1px rgba(255, 255, 255, 0.7);
  width: 6vh;
  height: 6vh;
  display: flex;
  align-items: "center";
  border-radius: 50%;
}

.shadow-update-button {
  box-shadow: -10px -10px 20px rgba(255, 255, 255, 0.7),
    10px 10px 20px rgba(0, 0, 0, 0.15) !important;
  border-radius: 3vh !important;
  font-family: "Google Sans", sans-serif !important;
  font-size: 2vh !important;
  text-transform: none !important;
  width: 17vh;
  height: 5.5vh;
  background-color: #ffffff !important;
}

.tool-bar {
  justifycontent: "space-between";
  padding: 8vh 0 2vh 0 !important;
  width: 100%;
}
